import React, { useEffect } from 'react'

const CartInfoPopup = (props) => {
  //     
  useEffect(() => {
    document.body.style.cssText = `
      position: fixed; 
      top: -${window.scrollY}px;
      overflow-y: scroll;
      width: 100%;`;
    return () => {
      const scrollY = document.body.style.top;
      document.body.style.cssText = '';
      window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
    };
  }, []);
  // 
  const closePopup = () => {
    props.isOpen(false);
  }

  // 
  const delProdFn = () => {
    props.actionFn(props.prodCode);
  }
  return (
    <div className='pop-bg'>
      <div className='pop-box'>
        <div className='pop-content'>
          <button className='pop-close-btn' onClick={closePopup}>
            <i className='icon icon-delete'></i>
          </button>
          <h4> ?</h4>
          <div className='pop-btn-box'>
            <button type='button' onClick={closePopup}></button>
            <button type='button' onClick={delProdFn}></button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default CartInfoPopup
